import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Dropdown } from "primereact/dropdown";
import { InputText } from 'primereact/inputtext';
import { addOnClickFunc, onRowEditComplete } from "../lib";        
import { Toolbar } from 'primereact/toolbar';
import { Button } from 'primereact/button';
import { useMemo } from "react";
import { OrganizationAddEmployeeForm } from "../../../../features/form";

export const Employees = ({ i18n,children,departments,employees,totalRecords,toastRef,dialogState}) => {
  const { t } = i18n;

    const tableHeader = useMemo(()=>{
      if(employees){
        const formProps = {i18n,dialogState,toastRef,departments}
        return <Toolbar className="table_header_toolBar" end={<Button size="small" icon="pi pi-plus" onClick={()=>addOnClickFunc(dialogState,t("pages.settings.organization.pages.addEmployee.addedEmployeesListTable.dialog.addEmployee.header"),OrganizationAddEmployeeForm,formProps)}/>} />
      }else return undefined
  }) 

  const departmentEditor = (options) => {
    return (
      <Dropdown 
        placeholder={`${t("repeatedWords.choose")} ${t("repeatedWords.department")}`} 
        optionLabel={"name"} 
        optionValue={"@id"} 
        options={departments} 
        value={options.value["@id"]} 
        onChange={(e) =>{options.editorCallback({"@id":e.target.value})}}/>
    );
};

  const textEditor = (options) => {
    return <InputText type="text" value={options.value} required onChange={(e) => options.editorCallback(e.target.value)} />;
};

  return (
    <div className="table_container">
      <DataTable  paginator value={employees} rows={7} className="table with_header" size={"normal"} editMode="row" onRowEditComplete={onRowEditComplete(toastRef,t)} totalRecords={totalRecords} header={tableHeader}>
        <Column field="id" header="NÂº"></Column>
        <Column field="name" editor={(options) => textEditor(options)} header={t("repeatedWords.name")}></Column>
        <Column field="surname" editor={(options) => textEditor(options)} header={t("repeatedWords.surname")}></Column>
        <Column field="department" editor={(options=>departmentEditor(options))} body={(rowData)=>rowData.department.name} header={t("repeatedWords.department")}></Column>
        <Column rowEditor className="text_align_center"></Column>
      </DataTable>
      {children}
    </div>
  );
};
