import { PageHeader } from "../../../../../shared/PageHeader";
import { PageFooter } from "../../../../../shared/PageFooter";
import { Dialog } from 'primereact/dialog';
import { useRef, useState } from "react";
import { EmployeesTable } from "../../../../../shared/table";
import { Toast } from 'primereact/toast';
import { TabView, TabPanel } from 'primereact/tabview';
import { ListOfUsersTable } from "../../../../../features/table";

export const AddEmployee = ({currentUser,i18n,departments}) => {
  const { t } = i18n;

  const dialogState = useState({visibility:false})

  const [dialogStateValue, setDialogStateValue] = dialogState;
  const toastRef = useRef(null);

  return <>
      <Dialog header={dialogStateValue?.header} className="dialog" visible={dialogStateValue.visibility} onHide={() => setDialogStateValue({visibility:false})}>{dialogStateValue?.content}</Dialog>
      <Toast ref={toastRef} />
      <main className="page settingsOrganization_page container_local">
      <PageHeader title={t("pages.settings.organization.contentCards.general.items.addEmployee")}></PageHeader>

        <div className="page_content">
          <TabView className="tabView" panelContainerClassName="padding0">
            <TabPanel header={`${t("repeatedWords.employees")}`}><EmployeesTable i18n={i18n} departments={departments} toastRef={toastRef} dialogState={dialogState}/></TabPanel>
            <TabPanel header={`${t("repeatedWords.users")}`}><ListOfUsersTable currentUser={currentUser} i18n={i18n} toastRef={toastRef} dialogState={dialogState}/></TabPanel>
          </TabView>
        </div>

        <PageFooter />
      </main>
    </>
  
};
