import { useEffect, useState } from "react";
import { Loading } from "../../../../shared/Loading";
import { AddEmployee as UI } from "./ui/AddEmployee";
import { Error } from "../../../../shared/Error";
import { useTranslation } from "react-i18next";
import { api } from "../../../../shared/api";
import { itemsPerPageForAllItems } from "../../../../entities/itemsPerPageForAllItems";

export const AddEmployee = ({currentUser}) => {
  const [componentStatus, setComponentStatus] = useState({ status: "loading" });

  const { i18n } = useTranslation();

  useEffect(() => {
    (async () => {
      const departmentsResponse = await api.departments.get({itemsPerPage:itemsPerPageForAllItems});

      const { warning, error } = departmentsResponse;

      if (error || warning) {
        setComponentStatus({
          status: "error",
          data: error || warning,
        });
        return;
      }

      setComponentStatus({status:"success",data:departmentsResponse})

    })();
  }, []);

  if (componentStatus.status === "loading") return <Loading />;
  if (componentStatus.status === "error") return <Error error={componentStatus.data}/>;
  if(componentStatus.status === "success") return <UI currentUser={currentUser} i18n={i18n} departments={componentStatus.data["hydra:member"]}/>;
  
};

export default AddEmployee;
