import { useEffect, useState } from "react";
import { Employees as UI } from "./ui/Employees";
import { api } from "../../api";
import { Loading } from "../../Loading";
import { Error } from "../../Error";
import { itemsPerPageForAllItems } from "../../../entities/itemsPerPageForAllItems";

export const Employees = ({ i18n, departments, toastRef, dialogState }) => {
  const [componentStatus, setComponentStatus] = useState({ status: "loading" });

  useEffect(() => {
    (async () => {
      const employeesResponse = await api.employees.get({itemsPerPage:itemsPerPageForAllItems});

      const { warning, error } = employeesResponse;

      if (error || warning) {
        setComponentStatus({
          status: "error",
          data: error || warning,
        });
        return;
      }

      setComponentStatus({ status: "success", data: employeesResponse });
    })();
  }, []);

  if (componentStatus.status === "loading") return <UI i18n={i18n}><Loading/></UI>;
  if (componentStatus.status === "error") return <UI i18n={i18n}><Error error={componentStatus.data}/></UI>;

  if (componentStatus.status === "success") 
    return <UI i18n={i18n} departments={departments} employees={componentStatus.data["hydra:member"]} totalRecords={componentStatus.data["hydra:totalItems"]} toastRef={toastRef} dialogState={dialogState}/>
};
