import { isChangesAfterTableEdit } from "../../../lib";
import { toastMessageConfig } from "../../../../entities/toastMessageConfig";
import { api } from "../../../api";

export const onRowEditComplete = (toastRef,t) => async (e) =>{
        
    const {data, newData} = e
    const {id,name, surname}  = newData

    if(!name || !surname ){
      toastRef.current.show({ severity: 'error', summary: t("repeatedWords.error"), detail: t("error.user.table.row.update.emptyFields.message"),life:toastMessageConfig.life })
    return
    }

    const isChanges = isChangesAfterTableEdit(data,newData)
    
    if(isChanges){
    newData.department = newData.department["@id"]
    const updateEmployeeResponse = await api.employees.update(id,newData)

    const {warning,error} = updateEmployeeResponse

    if(warning){
      toastRef.current.show({ severity: 'warn', summary: warning.title, detail: warning.message,life:toastMessageConfig.life})
      return
    }

    if(error){
      toastRef.current.show({ severity: 'error', summary: error.title, detail: error.message,sticky:true })
      return
    }

    toastRef.current.show({ severity: 'success', summary: t("repeatedWords.success"), detail: t("message.successfulOperation.message"),life:toastMessageConfig.life})
    return

    } else if(!isChanges){
      toastRef.current.show({ severity: 'error', summary: t("repeatedWords.error"), detail: t("error.user.table.row.update.noChanges.message"),life:toastMessageConfig.life })
    }
    
}